.App {
  text-align: center;
  width: 90%;
  max-width: 900px;
  margin: 0 auto;
}

.center {
  display: flex;
  justify-content: center;
}

.particles {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
}

.container {
  display: flex; /* or inline-flex */
  justify-content: space-between;
  flex-wrap: nowrap;
}
